.errorLogin {
  color: orange;
}
.loginFormWrapper{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 1px solid #dddddd;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1), 0 8px 16px rgba(0, 0, 0, .1);
  max-width: 500px;min-height: 400px;
  margin: 0 auto;
  padding: 10px;
  border-radius: 5px;
}
.loginTitle{
  margin: 0;
}
.forgot{
  text-decoration: none;
  color: #ffffff;
  font-size: 16px;
 float: right;
  &:hover{
    color: orange;
  }
}
.btn{
  margin-top: 20px;
}